import { useEffect, useState, Suspense } from "react";
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import FullscreenIcon from '@mui/icons-material/Fullscreen';
import FullscreenExitIcon from '@mui/icons-material/FullscreenExit';
import * as THREE from 'three';
import { Canvas, useLoader } from '@react-three/fiber';
import { OrbitControls, Html, useProgress } from '@react-three/drei';

const Demo = () => {
    const image = 'https://demo.hengshicoin.com/demo-360/images/sample.jpg';

    const [canvasHeight, setCanvasHeight] = useState('80vh');
    const [isFullscreen, setIsFullscreen] = useState(false);

    const ThreeImage = () => {
        const texture = useLoader(THREE.TextureLoader, image)
        texture.center = new THREE.Vector2(0.5, 0.5);
        texture.rotation = Math.PI;
        texture.flipY = false;
        return (
          <mesh>
            <sphereBufferGeometry attach="geometry" args={[800, 60, 40]} />
            <meshBasicMaterial attach="material" map={texture} side={THREE.BackSide} />
          </mesh>
        )
    };
    
    const fsChangeHandler = () => {
        if (document.fullscreenElement !== null) {
        }
        else {
            setIsFullscreen(false);
            setCanvasHeight('80vh');
        }
    };

    const onDoubleClick = (e) => {
        if(isFullscreen === true) {
            exitFullscreen();
        }
        else {
            goFullscreen(e);
        }
    };
  
    const goFullscreen = () => {
        var elem = document.getElementById("360image-canvas");
        elem?.requestFullscreen();
        setIsFullscreen(true);
        setCanvasHeight('100vh');
    };

    const exitFullscreen = () => {
        document.exitFullscreen();
        setIsFullscreen(false);
        setCanvasHeight('80vh');
    };

    const Loader = () => {
        const { progress } = useProgress();
        let p = progress.toFixed(2);
        return <Html><p className='threejsloader'>{p}%</p></Html>
    };

    document.addEventListener('fullscreenchange', fsChangeHandler, false);

    return (
        <Grid container sx={{pt:3}}>
            <Grid item xs={12} textAlign="center">
                <Box id="360image-canvas">
                    <Box>
                        <Canvas
                            onDoubleClick={onDoubleClick}
                            camera={{ position: [0, 0, .01] }}
                            style={{ width: "100%", height: canvasHeight }}>
                            <Suspense fallback={<Loader />}>
                                <ThreeImage />
                            </Suspense>
                            <OrbitControls enableZoom={true} enablePan={true} enableDamping dampingFactor={0.2} rotateSpeed={-0.25} />
                        </Canvas>
                    </Box>
                    {isFullscreen === true
                        ?
                        <Box className="threejs360image-exitFullscreen">
                            <IconButton onClick={exitFullscreen}>
                                <FullscreenExitIcon />
                            </IconButton>
                        </Box>
                        :
                        <Box className='threejs360image-goFullscreen'>
                            <IconButton onClick={goFullscreen}>
                                <FullscreenIcon />
                            </IconButton>
                        </Box>
                    }
                </Box>
            </Grid>
        </Grid>
    )
}

export default Demo;
