import Grid from '@mui/material/Grid';

const DemoImageWithInfo = () => {
    return (
        <Grid container sx={{pt:3}}>
            <Grid item xs={12} textAlign="center">
                <iframe title="demo-imagewithinfo" width="100%" height="680px" allowFullScreen src="html/demo.html" frameBorder="0"></iframe>
            </Grid>
        </Grid>
       
    )
};

export default DemoImageWithInfo;